h1,h2,h3,h4,h5,h6 {
    line-height: 1.2;
    margin: 0 0 em(20px, 50px);
    font-family: caveat, serif;
    font-weight: 400;
}

h1 {
    font-size: rem(50px);
}

p {
    margin: 0 0 1em;
    max-width: 100ch;
}

body {
    margin: 0;
    background-image: url(./assets/img/toy5.JPG);
    background-attachment: fixed;
    background-size: 100% 100%;
    background-repeat: no-repeat;
    margin-bottom: 50px;
}

img {
    width: 100%;
}

.main-container {
    display: grid;
    grid-template-columns: 50px 1fr 50px;

    > * {
        grid-column: 2;
    }
}

.control-container{
    display: flex;
    flex-direction: row;
    gap: 20px;
}

.toy-filter{
    width: 45%;
}
.toy-add-container .toy-app .toy-form{

    width: 45%
}    



    
    input{
        border: none;
        border-radius: 9px;
        margin: 5px;
        box-shadow: 0 0 5px 2px #0000004d;
        height: 20px;
    }
    
    select{
        border: none;
        border-radius: 9px;
        margin: 5px;
        box-shadow: 0 0 5px 2px #0000004d;
        height: 20px;
        width: 50%;
        margin: 0 auto;
    }

      
 
      .green{
          color: rgb(28, 117, 28);
          font-weight: bold;
      }
    
      .red{
          color: rgb(143, 38, 38);
          font-weight: bold;
    
      }
      .btn-control-toy{
          display: flex;
          flex-direction: row;
      }