.toy-container{
    display: flex;
    /* height: 300px; */
    width: 300px;
    border-radius: 20px;
    background-color: #ffffffab;
    font-size: 18px;
    flex-direction: column;
    justify-content: start;
    align-items: center;
    padding: 10px;
  

    .toy-preview-details{
        justify-content: start;
        align-items: center;
        display: flex;
    flex-direction: column;
    }

    .btn-control-toy{
        margin-bottom: 10px;
    }

    &:hover{
        background-color: darken( #ffffffab, 10%);
    }
}
a{
    display: inline;
}