
li{
    list-style: none;
  }
  
  .toy-list{
    display: grid;
 
    grid-row-gap: 15px;
    row-gap: 15px;

    grid-column-gap: 15px;
    column-gap: 15px;
    grid-template-columns: repeat( auto-fit, minmax(300px, 1fr) );
    margin: 10px;
  }
  
  ul{
    margin: 0;
    padding: 0;
  }