.nav > * {
  margin-inline-end: 20px;
  &:hover {
    font-size: larger;
    transition: 0.3s;
    color: (darken($clr5, 20%));
  }
}

header {
  background-color: white;
  inset: 0;
  padding: 10px 50px;
  display: grid;
  grid-template-columns: $layoutPadding 1fr $layoutPadding;

  > * {
    grid-column: 2;
  }
  h1 {
    color: (darken($clr5, 20%));
  }
}

.user{
  display: inline;
}
